/* Include Font Awesome in your HTML head */
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.0.0-beta3/css/all.min.css');

/* Custom Dropdown Container */
.custom-dropdown {
  position: relative; /* Position options absolutely within container */
  width: 400px; /* Width of the dropdown */
  cursor: pointer;
  margin-left: 10px;
}

/* Dropdown Selected Box */
.custom-dropdown-selected {
  margin: 5px 0;
  width: 100%;
  display: flex;
  /* justify-content: space-between; */
  align-items: center;
  background-color: #26235a;
  /* padding: 10px; */
  border-radius: 8px;
}
.custom-dropdown-selected span{
  font-size: 22px;
  font-weight: bold;
}

/* Font Awesome Arrow Styling */
.custom-dropdown-arrow {
  font-size: 12px; /* Adjust size */
  color: #ccc; /* Light gray color */
  transition: transform 0.3s ease; /* Smooth transform transition */
}

/* Rotate arrow when dropdown is open */
.custom-dropdown.open .custom-dropdown-arrow {
  transform: rotate(90deg); /* Rotate arrow */
}

/* Dropdown Options Container */
.custom-dropdown-options {
  width: 330px;
  padding: 0 4px;
  display: none; /* Hide by default */
  position: absolute; /* Position below selected box */
  background-color: #1d1c49; /* Darker background for options */
  border-radius: 10px; /* Rounded corners */
  z-index: 99; /* Ensure it appears above other elements */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3); /* Shadow for options */
  max-height: 180px; /* Maximum height */
  overflow-y: auto; /* Scroll if needed */
  opacity: 100; /* Start hidden */
}

/* Show Dropdown on Click */
.custom-dropdown.open .custom-dropdown-options {
  display: block; /* Show options when open */
  opacity: 1; /* Fade in */
  transform: scaleY(1); /* Scale up */
}

/* Dropdown Option */
.custom-dropdown-option {
padding: 10px 0;
  display: flex; /* Align icon and text */
  align-items: center; /* Center items vertically */
  cursor: pointer; /* Pointer cursor */
  transition: background-color 0.3s ease; /* Smooth background color transition */
}



